<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="角色名称：" required>
          <el-input disabled style="width:400px;" v-model="form.roleName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="角色描述：">
          <el-input disabled style="width:400px;" type="textarea" v-model="form.roleDescription" maxlength="50" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="菜单授权：" required>
          <el-tree
            disabled
            :data="tree"
            show-checkbox
            node-key="id"
            ref="tree"
            highlight-current
            :default-expanded-keys="openIds"
            :default-checked-keys="openIds"
            :props="defaultProps">
          </el-tree>
        </el-form-item>
        <div class="flex-row-center">
          <el-button @click="cancle">返 回</el-button>
        </div>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findAll  ,findById} from '@/api/systemSetting/roleManage'
export default {
  data(){
    return{
      form:{},
      tree: [],
      defaultProps: {
        children: 'list',
        label: 'name'
      },
      addLoading:false,
      openIds:[]
    }
  },
  created(){
    this.getTree()
    this.getData()
  },
  methods:{
    getData(){
      findById({id:this.$route.query.id}).then(res=>{
        console.log('角色详情',res)
        if(res.code == 200){
          this.form = res.data
          this.openIds = res.data.menuIds
        }
      })
    },
    getTree(){
      findAll().then(res=>{
        console.log('目录树',res)
        if(res.code == 200){
          res.data.forEach(e=>{
            e.disabled = true;
            e.list.forEach(item => {
              item.disabled = true;
            })
          })
          this.tree = res.data
        }else{
          this.$message.error(res.message);
        }
      })
    },
    cancle(){
      this.$router.push({path:'/systemSetting/roleManage'})
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>